body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.container__camera-overlay {
  width: 90%;
  max-width: 600px;
  border: 3px solid #0076FF;
  border-radius: 14px;
  height: 200px;
  z-index: 100;
  left: 4%;
  position: fixed;
  top: 33%;
}

.btn {
  background-color: #0076FF;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 10px 20px;
  width: 100%;
  font-weight: 600;
  font-size: 0.9rem;
}
.imgback {
  margin-right: 24rem;
  margin-bottom: 12rem;
  width: 35px;
  height: 35px;
  z-index: 10;
  position: absolute;
}

.video-mirror {
  transform: scaleX(-1);
  -webkit-transform: scaleX(-1);
  transform-origin: center;
  -webkit-transform-origin: center;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .imgback {
    margin-right: 20rem;
    margin-bottom: 20rem;
    width: 35px;
    height: 35px;
    z-index: 10;
    position: absolute;
  }
  .container__camera {
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .container__camera-camera {
    visibility: visible;
    max-height: 238px;
    max-width: 440px;
    width: 280px;
    height: 180px;
    /* position: absolute; */
    object-fit: cover;
    border: 5px solid #0076FF;
    border-radius: 16px;
    padding: 0 1.6rem;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .container__camera {
    position: relative;
