.progress-bar {
  position: absolute;
  background-color: red;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
.top {
  top: 0;
  left: 0;
  width: 0;
  height: 5px;
  animation-name: progressBarTop;
  animation-duration: 2s;
  position: absolute;
  z-index: 10;
}

.right {
  top: 0;
  right: 0;
  width: 5px;
  height: 0;
  animation-name: progressBarRight;
  animation-duration: 2s;
  animation-delay: 0s;
  position: absolute;
  z-index: 10;
}

.bottom {
  bottom: 0;
  right: 0;
  width: 0;
  height: 5px;
  animation-name: progressBarBottom;
  animation-duration: 2s;
  animation-delay: 0s;
  position: absolute;
  z-index: 10;
}

.left {
  bottom: 0;
  left: 0;
  width: 5px;
  height: 0;
  animation-name: progressBarLeft;
  animation-duration: 2s;
  animation-delay: 0s;
  position: absolute;
  z-index: 10;
}

@keyframes progressBarTop {
  0% {
    width: 0;
    background-color: red;
  }
  100% {
    width: 100%;
    background-color: blue;
  }
}

@keyframes progressBarRight {
  0% {
    height: 0;
    background-color: red;
  }
  100% {
    height: 100%;
    background-color: blue;
  }
}

@keyframes progressBarBottom {
  0% {
    width: 0;
    background-color: red;
  }
  100% {
    width: 100%;
    background-color: blue;
  }
}

@keyframes progressBarLeft {
  0% {
    height: 0;
    background-color: red;
  }
  100% {
    height: 100%;
    background-color: blue;
  }
}
